import {Component, OnInit, ViewContainerRef} from '@angular/core';
import * as $ from 'jquery';
import {HttpClient} from '@angular/common/http';
import {ToastsManager} from "ng2-toastr";
import {Router} from '@angular/router';

@Component({
  selector: 'app-homepage',
  templateUrl: './homepage.component.html',
  styleUrls: ['./homepage.component.css']
})
export class HomepageComponent implements OnInit {

  firstName: string = '';
  lastName: string = '';
  email: string = '';
  password: string = '';
  username: string = '';

  loginEmail: string = '';
  loginPassword: string = '';

  file: any = '';
  regexp: any = '';

  insertId: any = '';

  loggedIn: boolean = false;


  _imageUrl: any = '';
  _firstName: any = '';
  _lastName: any = '';
  _email: any = '';
  _username: any = '';



  constructor(private http: HttpClient, public toastr: ToastsManager, vcr: ViewContainerRef, public router: Router) {
    this.toastr.setRootViewContainerRef(vcr);
  }

  ngOnInit() {



    if(localStorage.getItem('id')){
      if(parseInt(localStorage.getItem('id')) > 0){
        this.loggedIn = true;
        this.http.post("http://thethinkcity.com:3000/api/get-user",
          {
            'id': localStorage.getItem('id')
          })
          .subscribe(
            (val) => {
              console.log(val['data']);
              if(val['status'] == 'Error'){
                this.toastr.warning("Your session expired!", 'Error');
                localStorage.removeItem('id');
              }
              else{
                this._imageUrl = val['data'].profile_picture;
                this._firstName = val['data'].FirstName;
                this._lastName = val['data'].LastName;
                this._email = val['data'].EmailAddress;
                this._username = val['data'].UserName;
              }
            },
            response => {
              console.log("POST call in error", response);
            },
            () => {
              // console.log("The POST observable is now completed.");
            });
      }
    }
    else{
      this.loggedIn = false;
    }


    $('#openLogin').click(()=>{
      $('#signupModalCloseBtn').click();
    });
  }

  signup(){
    this.regexp = new RegExp(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/);
    if(this.firstName.length == 0){
      this.toastr.error('First Name Required!', 'Field Required!');
    }
    else if(this.lastName.length == 0){
      this.toastr.error('Last Name Required!', 'Field Required!');
    }
    else if(this.username.length == 0){
      this.toastr.error('User Name Required!', 'Field Required!');
    }
    else if(this.email.length == 0){
      this.toastr.error('Email Required!', 'Field Required!');
    }
    else if(!(this.regexp.test(this.email))){
      this.toastr.error('Email not in correct format!', 'Field Required!');
    }
    else if(this.password.length == 0){
      this.toastr.error('Password Required!', 'Field Required!');
    }
    else if(this.file == '' || this.file == undefined){
      this.toastr.error('Profile Picture Required!', 'Field Required!');
    }
    else {
      this.http.post("http://thethinkcity.com:3000/api/signup",
        {
          "firstName": this.firstName,
          "lastName": this.lastName,
          "email": this.email,
          "password": this.password,
          "username": this.username,
          "profilePic": this.file
        })
        .subscribe(
          (val) => {
            console.log(val);
            if(val['status'] == 'Error'){
              this.toastr.error(val['data'], 'Error!');
            }
            else{
              this.insertId = val['data'];
              $('#signupModalCloseBtn').click();
              this.firstName = '';
              this.lastName = '';
              this.email = '';
              this.password = '';
              this.username = '';
              this.file = '';
              this.toastr.success('Signup Sucessful', 'Sucess!');
            }
          },
          response => {
            console.log("POST call in error", response);
          },
          () => {
            // console.log("The POST observable is now completed.");
          });


    }
  }

  login(){
    this.regexp = new RegExp(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/);
    if(this.loginEmail.length == 0){
      this.toastr.error('Email Required!', 'Field Required!');
    }
    else if(!(this.regexp.test(this.loginEmail))){
      this.toastr.error('Email not in correct format!', 'Field Required!');
    }
    else if(this.loginPassword.length == 0){
      this.toastr.error('Password Required!', 'Field Required!');
    }
    else {
      this.http.post("http://thethinkcity.com:3000/api/login",
        {
          "email": this.loginEmail,
          "password": this.loginPassword
        })
        .subscribe(
          (val) => {
            console.log(val);
            if(val['status'] == 'Error'){
              this.toastr.error(val['data'], 'Error!');
            }
            else{
              this.insertId = val['data'];
              localStorage.setItem("id",this.insertId );
              this.toastr.success('Signin Sucessful', 'Sucess!');
              $('#loginModalCloseBtn').click();
              $('body').css('padding-right', '0px !important');
              setTimeout(()=>{
                window.location.href = "/dashboard";

              }, 1000)
            }
          },
          response => {
            console.log("POST call in error", response);
          },
          () => {
            // console.log("The POST observable is now completed.");
          });


    }
  }

  showMenu(){
    if($('#dropdown').css('display') == 'none') {
      $('#dropdown').css('display', 'block');
    }
    else{
      $('#dropdown').css('display','none');
    }
  }



  getBase64(file) {
    var reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload =  () => {
      this.file = reader.result;
      console.log(this.file);
    };
    reader.onerror = function (error) {
      console.log('Error: ', error);
    };
  }

  uploadFile(event) {

    console.log(event.target.files[0]);

    if(typeof event.srcElement == 'undefined'){
      var fileSize =event.target.files[0].size;
      var fileData =event.target.files[0];
    }
    else{
      var fileSize = event.srcElement.files[0].size;
      var fileData = event.srcElement.files[0];

    }


    if(fileSize > 8000){
      this.toastr.warning("Image file size exceeds limit!", 'Error!');
    }
    else{
      this.getBase64(fileData);
    }

  }

  gotoDashboard(){
    this.router.navigate(['dashboard']);
  }

  logout(){
    localStorage.removeItem('id');
    this.toastr.success("Logout Sucessful!", 'Success!');
    setTimeout(()=>{window.location.reload()},1000);
  }
}



