  save(){
    this.from = $('#from').val();
    this.to = $('#to').val();

    this.fromDate = $('#fromDate').val();


    var isAddable = true;



    if(this.from == this.to){
      this.toastr.error("From and To airport can not be same!", 'Error!');
      $(".bootstrap-select").css("border-bottom","1px solid red");
      isAddable = false;
    }else{
      $(".bootstrap-select").css("border-bottom","none");
    }

    if((this.adults).length == 0 || (this.sendEvery).length == 0){
      if((this.adults).length == 0 ){
        $("#sel1").addClass("selectError");
      }
      else{
        $("#sel1").removeClass("selectError");
      }
      if((this.sendEvery).length == 0){
        $("#sel2").addClass("selectError");
      }
      else{
        $("#sel2").removeClass("selectError");
      }
      this.toastr.error("Please Complete required fields!", 'Error!');
      isAddable = false;
    }
    else{
      $("#sel1").removeClass("selectError");
      $("#sel2").removeClass("selectError");
    }


    if(this.tripType != "oneway"){
      this.toDate = $('#toDate').val();
    }
    else{
      this.toDate = '-';
    }


        if(this.tripType != "oneway") {
          if ($('#toDate').val() < $('#fromDate').val()) {
            this.toastr.error("Invalid Travel Dates!", 'Error!');
            $("#fromDate").css("color", "red");
            $("#toDate").css("color", "red");
            isAddable = false;
          }
          else {
            $("#fromDate").css("color", "#555");
            $("#toDate").css("color", "#555");
          }
        }



    var oneway = true;
    var round = false;
    if(this.tripType == "oneway"){
      oneway = true;
      round = false;
    }else if(this.tripType == "round"){
      oneway = false;
      round = true;
    }




    this.data = {
      "id": localStorage.getItem("id"),
      "from": this.from,
      "to": this.to,
      "departureDate": this.fromDate,
      "returnDate": this.toDate,
      "oneway": oneway,
      "miles": this.miles,
      "exactDates": '-',
      "passengerCount": this.adults,
      "stops": this.connections,
      "duration": '-',
      "airlines": '-',
      "travelClass": this.class,
      "isActive": true,
      "sendEvery": this.sendEvery
    };

    console.log(this.data);
    if((this.adults).length == 0){
      this.data.passengerCount = '-';
    }
    if((this.class).length == 0){
      this.data.travelClass = '-';
    }
    if((this.connections).length == 0){
      this.data.stops = '-';
    }
    if((this.miles).length == 0){
      this.data.miles = '-';
    }
    if($('#selectClass').attr('disabled') == 'disabled'){
      this.connections = '';
      this.miles = '';
    }
    if($('#selectConnections').attr('disabled') == 'disabled'){
      this.class = '';
      this.miles = '';
    }
    if($('#selectMiles').attr('disabled') == 'disabled'){
      this.connections = '';
      this.class = '';
    }

    if(isAddable) {
      this.http.post("http://thethinkcity.com:3000/api/save-alert",
        this.data)
        .subscribe(
          (val) => {
            if (val == 1) {
              this.toastr.success("Alert Saved!", 'Success!');
              setTimeout(() => {
                window.location.reload();
              }, 1000)
            }
            else {
              this.toastr.error("An unexpected error occured!", 'Error!');
            }
            console.log(val);
          },
          response => {
            this.toastr.error("An unexpected error occured!", 'Error!');
            console.log("POST call in error", response);
          },
          () => {
            // console.log("The POST observable is now completed.");
          });
    }


  }




