  showHideAdvance(){
    if(this.isAdvancedVisible){


      $('#add').removeClass("slideInDown");
      $('#add').removeClass("slideOutUp");
      $('#add').addClass("slideOutUp");
      setTimeout(()=>{
        $('#add').css("display","none");
        $('#btnSave1').css("display","block");
      },1000);
      this.isAdvancedVisible = false;
    }
    else{
      $('#add').css("display","block");
      $('#add').removeClass("slideInDown");
      $('#add').removeClass("slideOutUp");
      $('#add').addClass("slideInDown");
      $('#btnSave1').css("display","none");
      this.isAdvancedVisible = true;
    }
  }



